import React, { useEffect, useState } from "react";
import { Row, Col, Card, Form, DatePicker, Button, Radio, Select, Empty, Modal   } from "antd";
import { Table, Space } from "antd";
import { connect } from "react-redux";
import moment from "moment";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import EmptyImage from "../../../../assets/images/app/empty-list.svg";
import {
  InputChangeValue,
  getReportList,
  clearData,
} from "../../../../store/reports/reportsSlice";
import {
  getMiniCounters,
  setPurchaseAvailableStockParams,
  getPurchaseAvailableStock,
  getAllCounters,
} from "../../../../store/mini/miniSlice";
import {
  RiAlertLine,
} from "react-icons/ri";

function StockTransferOutReport(props) {
  const [form] = Form.useForm();
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";

    const transferStatusList = [
      { id: 1, name: "Pending" },
      { id: 2, name: "Approved" },
      { id: 3, name: "Rejected" },
      { id: 4, name: "Accepted" },
      { id: 5, name: "To Counter Rejected" },
    ];

  useEffect(() => {
    props.getMiniCounters();
    props.getAllCounters();
    props.clearData();
  }, []);

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const onFinish = () => {
    if(props.reportParamsData.file_format == "HRMS"){
      var { file_format, ...params } = props.reportParamsData;
      var objData = {
        file_format: 2,
        model_name: "HRMS Daily Attendance",
      };
    }else{
    var { file_format, ...params } = props.reportParamsData;
    var objData = {
      file_format: file_format,
      model_name: "StockTransferOut",
    };
  }
    props.getReportList({
      objData,
      ParamsData: {
        ...params,
        start_date:
          params.start_date != null
            ? moment(params.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          params.end_date != null
            ? moment(params.end_date).add(1, "days").format("YYYY-MM-DD")
            : "",
      },
    });
  };
  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  const columns = props.columnsList.map((column) => {
    return {
      title: column,
      dataIndex: column,
    };
  });
  columns.unshift({ title: "S.No", dataIndex: "index" });

  const data = props.reportDataList.map((row, key) => ({
    ...row,
    index: key + 1,
  }));


  function checkCounter() {
    if (props.reportParamsData?.counter_id == props.reportParamsData.tocounter_id) {
      Modal.error({
        icon: (
          <span className="remix-icon">
            <RiAlertLine />
          </span>
        ),
        title: <h5 className="hp-mb-0 hp-font-weight-500">Error</h5>,
        content: (
          <div>
            <p className="hp-p1-body hp-text-color-black-80">
              Sorry! Not allowd to same counter
            </p>
          </div>
        ),
      });
      props.InputChangeValue({
        key: "tocounter_id",
        value: null,
      });
    }
  }

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.reportParamsData?.start_date,
      end_date: props.reportParamsData?.end_date,
      file_format: props.reportParamsData?.file_format,
      counter_id: props.reportParamsData?.counter_id,
      tocounter_id: props.reportParamsData?.tocounter_id,
      transferstatus: props.reportParamsData?.transferstatus,
    });
  }, [props.reportParamsData]);
  return (
    <Row gutter={[12, 12]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Stock Transfer Out Report"
        // addModelTile="Add Zone"
        goBack={true}
        // setIsModalVisible={props.isModelVisible}
        // isModalVisible={props.model}
      />
      <Col span={24}>
        <Card>
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item
                  label="From Date"
                  name="start_date"
                  rules={[
                    {
                      required: true,
                      message: "Please select Your From Date!",
                    },
                  ]}
                >
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment(),
                    })}
                    onChange={(date) => {
                      props.InputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.InputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item
                  label="To Date"
                  name="end_date"
                  rules={[
                    {
                      required: true,
                      message: "Please select Your To Date!",
                    },
                  ]}
                >
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(
                        props.reportParamsData?.start_date
                      ).format("YYYY-MM-DD"),
                    })}
                    disabled={
                      props.reportParamsData.start_date == null ||
                      props.reportParamsData.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.InputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item
                  label="From Counter"
                  name="counter_id"
                  // rules={[
                  //   {
                  //     required: true,
                  //     message: "Please Select From Counter",
                  //   },
                  // ]}
                  hasFeedback
                >
                  <Select
                    showSearch
                    placeholder="Select From Counter"
                    optionFilterProp="Counter"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onBlur={(value) => {
                      props.getPurchaseAvailableStock({
                        ...props.purchaseAvailableStockParams,
                        counter: props.reportParamsData?.counter_id,
                        page: 1,
                      });
                      props.setPurchaseAvailableStockParams({
                        ...props.purchaseAvailableStockParams,
                        counter: props.reportParamsData?.counter_id,
                        page: 1,
                      });
                    }}
                    onChange={(value) => {
                      // props.GetCounterByID(value);
                      props.getPurchaseAvailableStock();
                      props.InputChangeValue({
                        key: "counter_id",
                        value: value,
                      });
                    }}
                    value={
                      props.reportParamsData.counter_id == null
                        ? ""
                        : props.reportParamsData?.counter_id
                    }
                  >
                    {props.miniCountersList.map((counter, index) => {
                      return (
                        <Option key={index} value={counter.id}>
                          {counter.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item
                  label="To Counter"
                  name="tocounter_id"
                  // rules={[
                  //   {
                  //     required: true,
                  //     message: "Please Select To Counter",
                  //   },
                  // ]}
                  hasFeedback
                >
                  <Select
                    showSearch
                    placeholder="Select To Counter"
                    optionFilterProp="Counter"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onBlur={() => {
                      checkCounter();
                    }}
                    onChange={(value) => {
                      props.InputChangeValue({
                        key: "tocounter_id",
                        value: value,
                      });
                    }}
                    value={
                      props.reportParamsData.tocounter_id == null
                        ? ""
                        : props.reportParamsData?.tocounter_id
                    }
                  >
                    {props.allCountersList.map((tocounter, index) => {
                      return (
                        <Option key={index} value={tocounter.id}>
                          {tocounter.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={8} xl={6}>
                <Form.Item label="Transfer Status" name="transferstatus">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select a Transfer Status"
                    optionFilterProp="transferstatus"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.InputChangeValue({
                        key: "transferstatus",
                        value: value,
                      });
                    }}
                    value={
                      props.reportParamsData.transferstatus == null
                        ? ""
                        : props.reportParamsData?.transferstatus
                    }
                  >
                    {transferStatusList.map((transfer, index) => {
                      return (
                        <Option value={transfer.id}>{transfer.name}</Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={10} xl={10}>
                <Form.Item
                  label="Report Format"
                  name="file_format"
                  rules={[
                    {
                      required: true,
                      message: "Please select Your Report Format!",
                    },
                  ]}
                >
                  <Radio.Group
                    name="file_format"
                    onChange={ChangeHandeler}
                    value={props.reportParamsData?.file_format}
                  >
                    <Radio.Button value={0}>CSV</Radio.Button>
                    <Radio.Button value={2}>XLSX</Radio.Button>
                    {/* <Radio.Button value={"HRMS"}>HRMS</Radio.Button> */}
                    <Radio.Button value={5}>VIEW</Radio.Button>
                  </Radio.Group>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12} offset={12}>
                <Button
                  // block
                  type="secondary"
                  className="hp-submit-btn hp-float-right hp-mt-10 hp-ml-10"
                  onClick={() => {
                    props.clearData();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
                <Button
                  // block
                  type="primary"
                  className="hp-submit-btn hp-float-right hp-mt-10"
                  disabled={props.loading}
                  loading={props.loading}
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Generate Report
                </Button>
              </Col>
            </Row>
          </Form>
        </Card>
      </Col>

      <Col span={24}>
      {props.reportView && (
          <>
        {props.reportDataList.length != 0 &&
        props.reportParamsData.file_format == 5 ? (
          <>
            <Card>
              <Row className="hp-mb-12">
                <Col span={24}>
                  <Table
                    columns={columns}
                    dataSource={data}
                    scroll={{ x: 500 }}
                    pagination={false}
                    size="middle"
                    loading={props.loading}
                  />
                </Col>
              </Row>
            </Card>
          </>
        ) : (
          <Card>
            <Empty
              className="hp-m-32"
              image={EmptyImage}
              imageStyle={{
                height: 160,
              }}
              description={<h5>Your Stock Transfer Out is empty</h5>}
            ></Empty>
          </Card>
        )}
        </>
        )}
      </Col>
    </Row>
  );
}

function mapStateToProps({ reports, mini,  }) {
  return {
    modelName: reports.modelName,
    reportParamsData: reports.reportParamsData,
    reportDataList: reports.reportDataList,
    loading: reports.loading,
    columnsList: reports.columnsList,
    allCountersList: mini.allCountersList,
    miniCountersList: mini.miniCountersList,
    purchaseAvaliableStockList: mini.purchaseAvaliableStockList,
    purchaseAvailableStockParams: mini.purchaseAvailableStockParams,
    reportView: reports.reportView
  };
}

export default connect(mapStateToProps, {
  InputChangeValue,
  getReportList,
  clearData,
  getAllCounters,
  setPurchaseAvailableStockParams,
  getPurchaseAvailableStock,
  getMiniCounters
})(StockTransferOutReport);
